*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 32pt;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.8);
  font-size: 14px;
}

.btn {
  display: block;
  font-size: 12px;
  border: 2px solid;
  border-radius: 25px;
}

.order_btn {
  padding: 10px 40px;
  color: #212864;
  border-color: #212864;
  transition: 0.2s;
}

.order_btn:hover {
  color: white;
  background-color: #212864;
}

.upper_head {
  max-width: 90vw;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 10px;
}

.logo p {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
}

.numbers,
.adress {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
}

.phone_number {
  font-size: 16px;
  font-weight: bold;
  color: #212864;
}

.social_icons {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}

.nav {
  background-color: #15193D;
  height: 55px;
}

.nav_bar,
.nav_catalog {
  max-width: 88vw;
  margin: 0 auto;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.nav_link {
  color: white;
  font-size: 14px;
  transition: 0.1s;
}

.nav_link:hover,
.nav_link:active {
  color: #FFC239;
}

.catalog {
  background-color: #8C93D1;
  height: 55px;
}
.catalog .vertical {
  height: 45px;
  width: 2px;
  background-color: rgba(255, 255, 255, 0.4);
}

.face {
  position: relative;
  height: 600px;
  background-image: url(../img/head_background.png);
  background-color: rgba(0, 0, 0, 0.4);
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: multiply;
}
.face .text {
  position: absolute;
  top: 10vh;
  left: 10vw;
}
.face .text h1 {
  max-width: 875px;
  color: white;
  font-size: 48px;
  font-weight: bold;
}
.face .text p {
  margin-top: 25px;
  font-family: "Montserrat";
  font-size: 16px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8);
  max-width: 470px;
}
.face .text .calculate_group {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}
.face .text .calculate_group a {
  color: white;
}
.face .text .calculate_group a #circle,
.face .text .calculate_group a #vector {
  transition: all 0, 1s ease;
}
.face .text .calculate_group a #Group_calculate {
  stroke: white;
}
.face .text .calculate_group a #Group_calculate:hover {
  stroke: #FFC239;
}
.face .text .calculate_group a:hover {
  color: #FFC239;
}

.tabs {
  max-width: 90vw;
  margin: 0 auto;
  margin-top: 80px;
}
.tabs h1 {
  font-size: 42px;
  font-weight: bold;
  margin-bottom: 20px;
}

.tabs__nav_btns {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 20px;
}

.tabs__nav_btn {
  font-size: 20px;
  font-weight: 500;
  color: #212864;
  padding: 10px 30px;
}

.tabs__nav_btn.active {
  background-color: #FFC239;
}

.tabs__item {
  display: none;
}

.tabs__item.active {
  display: block;
}

.slider_card {
  width: 285px;
  height: 355px;
  border-radius: 10px;
  background-color: rgba(152, 144, 144, 0.31);
  margin: 0 auto;
}
.slider_card .card_photo {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 285px;
  height: 140px;
  overflow: hidden;
}
.slider_card .card_photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.slider_card .card_content {
  color: black;
  width: 85%;
  margin: 0 auto;
}
.slider_card .card_content h2 {
  font-size: 18px;
  font-weight: bold;
}
.slider_card .card_content table {
  margin-top: 15px;
  margin-bottom: 20px;
  width: 100%;
}
.slider_card .card_content table tr {
  display: inline-block;
  width: 100%;
}
.slider_card .card_content table tr td {
  display: inline-block;
  width: 50%;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.8);
}
.slider_card .card_content .cost {
  font-weight: bold;
  font-size: 22px;
}

.swiper {
  max-width: 90vw;
}

.section_btn {
  margin: 0 auto;
  width: 310px;
  margin-top: 15px;
  margin-bottom: 20px;
}

.all_projects_btn {
  font-size: 16px;
  background-color: #212864;
  color: white;
  padding: 15px 30px;
  transition: 0.2s;
  text-align: center;
}

.all_projects_btn:hover {
  background-color: #FFC239;
  color: #212864;
  border: 1px solid #212864;
}

.family_slider .request {
  width: 25%;
}
.family_slider .family_slider_images {
  display: flex;
  justify-content: space-between;
}
.family_slider .family_slider_images .post {
  height: 144px;
}
.family_slider h2 {
  font-size: 38px;
  font-weight: 400px;
  max-width: 45vw;
  margin-bottom: 50px;
  margin-top: 30px;
}
.family_slider h2 span {
  font-weight: bold;
}
.family_slider .swiper-button-next,
.family_slider .swiper-button-prev {
  top: 80%;
}
.family_slider .swiper-button-prev {
  left: auto;
  right: 100px;
}
.family_slider .swiper-pagination {
  bottom: 91px;
  right: 50px;
  left: auto;
  width: 35px;
}

.family_slider_slide_1,
.family_slider_slide_2 {
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 0 auto;
  margin-top: 20px;
  max-width: 90vw;
  background-repeat: no-repeat;
  background-size: contain;
}

.family_slider_slide_1 {
  background-image: url(../img/second_slider_family_1.png);
}

.family_slider_slide_2 {
  background-image: url(../img/second_slider_family_2.png);
}

.benefits {
  margin: 0 auto;
  display: block;
  margin-top: 70px;
  margin-bottom: 40px;
  padding-top: 40px;
  width: 90%;
  background-image: url(../img/benefits_background.png);
  background-color: rgba(246, 246, 249, 0.5);
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: lighten;
}
.benefits h1 {
  width: 40%;
  font-weight: bold;
  text-align: left;
  margin-bottom: 20px;
  margin-left: 20px;
}
.benefits .benefits_slider {
  padding-bottom: 60px;
  padding-top: 10px;
  height: 670px;
  width: 90%;
  margin: 0 auto;
}
.benefits .benefits_slider .swiper-button-next, .benefits .benefits_slider .swiper-button-prev {
  top: auto;
  bottom: 1.5%;
}
.benefits .benefits_slider .benefits_slide {
  display: flex;
  height: calc((90% - 30px) / 4);
  flex-direction: row;
  align-items: center;
  width: 45%;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.8);
}
.benefits .benefits_slider .benefits_slide .slide_text {
  margin-left: 10px;
  width: 80%;
}
.benefits .benefits_slider .benefits_slide .slide_text h2 {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 10px;
}
.benefits .benefits_slider .benefits_slide .slide_text p {
  font-family: "Montserrat";
  font-size: 14px;
  color: rgba(0, 0, 0, 0.6);
}
.benefits .benefits_slider .benefits_slide .slide_image {
  margin-right: 10px;
}
.benefits .news {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 30px 0;
}
.benefits .news .news_container {
  width: 95%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 10px;
  flex-direction: row;
  flex-wrap: wrap;
}
.benefits .news .news_container .text {
  flex-shrink: 10;
  min-width: 310px;
  max-width: 80%;
}
.benefits .news .news_container .text h2 {
  font-size: 30px;
}
.benefits .news .news_container .text h2 span {
  font-weight: bold;
}
.benefits .news .news_container .text p {
  font-size: 14px;
  font-family: "Montserrat";
}
.benefits .news .news_container .button {
  flex-shrink: 0;
  text-align: center;
  min-width: 230px;
}
.benefits .news .news_container .button a {
  color: white;
  background-color: #212864;
  border: 2px solid #212864;
  padding: 10px 20px;
  font-weight: bold;
  transition: 0.2s;
}
.benefits .news .news_container .button a:hover {
  color: #212864;
  background-color: white;
}

.contact_us {
  position: relative;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 100px;
  max-width: 90%;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(../img/just_house.png);
}
.contact_us .request {
  width: 10%;
  position: absolute;
  right: 10%;
  bottom: 10%;
}
.contact_us .family_slider_images {
  display: flex;
  justify-content: space-between;
}
.contact_us .family_slider_images .post {
  height: 144px;
}
.contact_us h2 {
  font-size: 38px;
  font-weight: 400px;
  max-width: 45vw;
  margin-bottom: 50px;
  margin-top: 30px;
}
.contact_us h2 span {
  font-weight: bold;
}
.contact_us p {
  font-size: 18px;
  font-weight: 400px;
  max-width: 45vw;
  margin-top: 30px;
}

.reviews {
  margin-bottom: 50px;
}
.reviews .swiper-button-prev {
  left: 30%;
  right: auto;
}
.reviews .swiper-button-next {
  right: 30%;
  left: auto;
}

.reviews_slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 33%;
  background-color: rgba(104, 99, 134, 0.288);
  border-radius: 25px;
}
.reviews_slide h3 {
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 20px;
}
.reviews_slide p {
  font-size: 16px;
  font-family: "Montserrat";
  margin: 15px 10px;
}
.reviews_slide .horizontal_element {
  width: 50px;
  height: 3px;
  background-color: #FFC239;
  margin-top: 30px;
}